import Vue from 'vue'
import VueRouter from 'vue-router'
//导入组件
import Home from '../components/Home.vue'
import Login from '../components/Login.vue'
import Main from '../components/Main.vue'
import User from '../components/user.vue'
import Other from '../components/others.vue'
import Mall from '../components/Mall.vue'
import nullView from '../components/nullView.vue'




//使用vuerouter
Vue.use(VueRouter)
//路由与组件映射
const routes = [
  {
    path: '/',
    component: Main,
    children: [
      { path: '', component: Home },
      {
        path: 'mall', component: Mall,
        // 路由独享守卫：仅对 /admin 生效
        beforeEnter: (to, from, next) => {
          // 验证是否为管理员
          const isAdmin = localStorage.getItem('name') == 'admin'
          if (isAdmin) {
            next()
          }
          else {
            next('/403')
          }
        }
      },
      { path: 'user', component: User },
      { path: 'other/:id', component: Other },
    ]
  },
  {
    path: '/login',  // 修改路径为 /login
    name: 'Login',
    component: Login,
    meta: { public: true }  // 标记为公共页面
  },
  {
    path: '/403',
    name: '403',
    component: nullView
  }
]







//创建router实例
const router = new VueRouter({
  routes,
})


//------------解决跳转不让重定向问题
const originalPush1 = VueRouter.prototype.push;

VueRouter.prototype.push = function push(location, onResolve, onReject) {

  if (onResolve || onReject)

    return originalPush1.call(this, location, onResolve, onReject);

  return originalPush1.call(this, location).catch((err) => err);

};
//------------

// 在上面的 router/index.js 中继续添加
router.beforeEach((to, from, next) => {
  // 1. 获取本地存储的 token（登录成功后会存储）
  const token = localStorage.getItem('token')
  // 3. 如果用户已登录，却访问登录页 → 自动跳转到首页
  if (to.path === '/login' && token) {
    next('/')
    return
  }
  if (!token) {
    if (to.meta.public) {
      next();
      return;
    }
  }

  if (to.meta.public) {
    next();
    return;
  }

  // 4. 其他情况：正常访问
  next()
})









// 解决重复导航报错
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => {
    // 只屏蔽重复导航的错误，其他错误正常抛出
    if (err.name !== 'NavigationDuplicated') {
      throw err;
    }
  });
};
//对外暴露路由
export default router
