<template>
  <div>
    <el-dialog title="提示" :visible.sync="localVisible" width="30%" :before-close="handleClose">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select class="start" v-model="ruleForm.sex" placeholder="请选择您的性别">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="birth">
          <el-date-picker type="date" :picker-options="expireTimeOPtion" placeholder="选择日期" value-format="yyyy-MM-dd"
            v-model="ruleForm.birth" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="addr">
          <el-input placeholder="请输入地址" v-model="ruleForm.addr"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="display = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { addUser, editUser } from '@/api'
export default {
  props: {
    modelType: {
      required: true,
      type: Number,
      validator(value) {
        return [0, 1, 2].includes(value); // 限制值范围
      }
    },
    dialogVisible: {
      required: true,
      type: Boolean,
    },
    rows: {
      type: Object
    }
  },
  watch: {
    // 监听父组件传入的 visible 变化
    dialogVisible(val) {
      this.localVisible = val;
    },
    rows(val) {
      this.ruleForm = val
    }
  },
  data() {
    return {
      expireTimeOPtion: {
        disabledDate(time) {
          return time.getTime() > Date.now();  //如果没有后面的-8.64e7就是不可以选择今天的 
        }
      },
      localVisible: this.dialogVisible,
      ruleForm: {
        name: '',
        age: '',
        sex: '',
        addr: '',
        birth: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入您的名字', trigger: 'blur' },
          { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
        birth: [
          { required: true, message: '请选择日期', trigger: 'change' }
        ],
        age: [
          { required: true, trigger: 'blur' }
        ]
      },
    }
  },
  methods: {
    handleClose() {
      this.resetForm('ruleForm')
      this.$emit('update:dialogVisible', false); // 通知父组件关闭对话框
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        const age = new Date().getFullYear() - this.ruleForm.birth.split("-")[0]
        const status = this.ruleForm.age == age
        if (!status) {
          const action = await this.$msgbox({
            title: '消息',
            message: `建议您年龄填写: ${age}`,
            showCancelButton: true,
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning' // 加个警告图标更直观
          });

          // 3. 根据用户操作判断是否中断
          if (action === 'confirm') {
            // 点击确定→中断流程，不执行后续代码
            return false;
          }
        }
        if (valid) {
          if (this.modelType) {
            editUser(this.ruleForm).then(res => {
              if (res.status == 200) {
                this.$message({
                  message: '修改成功',
                  type: 'success'
                });
                this.$emit('submit', true)
              }
            })
          } else {
            addUser(this.ruleForm).then(res => {
              if (res.status == 200) {
                this.$emit('submit', true)
                this.$message({
                  message: '新建成功',
                  type: 'success'
                });
              }
            })
          }
          this.$emit('update:dialogVisible', false);
          this.resetForm(formName)
        } else {
          this.$message.error('error submit!');
          this.resetForm(formName)
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  mounted() {
  }
}
</script>

<style scoped></style>