// 封装菜单获取逻辑
export default {
  getMenu: (role = 'guest') => {
    try {
      // 根据角色过滤菜单
      const filteredMenus = role.body === 'admin'
        ? allMenus
        : allMenus.filter(menu => menu.permission === 'all' || menu.permission === role.body)

      return {
        code: 200,
        message: '获取菜单成功',
        data: filteredMenus
      }
    } catch (error) {
      return {
        code: 400,
        message: '获取菜单失败',
        error: error.message
      }
    }
  }
}


